}
body.body--hide {
  position: relative;
  overflow: hidden;
}

a {
  color: var(--color-dark);
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s;
}
a:hover,
a:hover .wrap-icon, a:active,
a:active .wrap-icon {
  color: var(--color-gray-dark-light);
}

hr {
  border-color: var(--border-color);
}

.wrap-icon {
  display: inline-flex;
  flex-shrink: 0;
  width: 20px;
  height: 20px;
}
@media screen and (min-width: 1448px) {
