}
@media screen and (min-width: 1024px) {
  .wrap-icon--tab-icon {
    display: none;
  }
}
.wrap-icon--fix-size {
  width: 24px;
  height: 24px;
}
.wrap-icon--rotate {
  transform: rotate(-180deg);
}
.wrap-icon--tag {
  width: 16px;
  height: 16px;
}
@media screen and (min-width: 1448px) {
  .wrap-icon--tag {
