  height: 20px !important;
}
.wrap-icon svg {
  width: 100%;
  height: 100%;
  margin: auto;
  transition: 0.3s;
}

.badges__row {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
}

.badge {
  display: inline-block;
  padding: 6px 10px;
  color: var(--color-white);
  font-weight: 600;
  font-size: 12px;
  line-height: 1;
  white-space: nowrap;
  background-color: var(--color-btn-hover);
  border-radius: 70px;
}
.badge--green-dark {
  background-color: var(--color-green-dark);
}
.badge--purple {
  background-color: var(--color-purple);
}
.badge--yellow {
  background-color: var(--color-yellow);
}
.badge--blue {
  background-color: var(--color-blue);
}
.badge--red {
