}

.custom-scrollbar-x {
  overflow-x: auto;
}
.custom-scrollbar-x::-webkit-scrollbar {
  height: 4px;
}
.custom-scrollbar-x::-webkit-scrollbar-track {
  background-color: transparent;
}
.custom-scrollbar-x::-webkit-scrollbar-thumb {
  background-color: #ced3d9;
  background-clip: padding-box;
  border: 4px solid rgba(0, 0, 0, 0);
  border-radius: 9999px;
}
.custom-scrollbar-y {
  overflow-y: auto;
}
.custom-scrollbar-y::-webkit-scrollbar {
  width: 12px;
}
.custom-scrollbar-y::-webkit-scrollbar-track {
  background-color: transparent;
}
.custom-scrollbar-y::-webkit-scrollbar-thumb {
  background-color: #ced3d9;
  background-clip: padding-box;
  border: 4px solid rgba(0, 0, 0, 0);
  border-radius: 9999px;
}

select {
  width: 100%;
  max-height: 50px;
  padding-right: 40px !important;
  background: url(../img/select_arrow.svg) var(--color-white) no-repeat calc(100% - 14.5px) !important;
  background-size: 24px !important;
  cursor: pointer;
  -webkit-appearance: none;
  appearance: none;
}

.select-wrap {
  position: relative;
}
.select-wrap .select {
  position: absolute;
  display: none;
  width: 1px;
  height: 1px;
  opacity: 0;
}

.custom-select--show .custom-select__current {
  border-color: var(--color-green);
}
.custom-select--show .custom-select__current::after {
  transform: rotate(-180deg) translateY(50%);
}
.custom-select--show .custom-select__list {
  margin-top: 5px;
  background-color: var(--color-white);
  border: 1px solid var(--color-border);
  border-radius: 8px;
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}
.custom-select__current {
  position: relative;
  width: 100%;
  max-height: 50px;
  padding: 13.5px 16px;
  padding-right: 48px !important;
  font-size: 16px;
  border: 1px solid var(--color-border);
  border-radius: 8px;
  cursor: pointer;
}
.custom-select__current::after {
  position: absolute;
  top: 50%;
  right: 14.5px;
  display: block;
  width: 24px;
  height: 24px;
  background: url(../img/select_arrow.svg) var(--color-white) no-repeat center;
  background-size: 24px !important;
  transform: translateY(-50%);
  transition: all 0.25s ease;
  content: "";
}
.custom-select__list {
  position: absolute;
  z-index: 2;
  width: 100%;
  padding: 16px 8px 16px 24px;
  overflow: hidden;
  border: 1px solid var(--color-gray-dark-light);
  visibility: hidden;
  opacity: 0;
  transition: all 0.25s ease;
  pointer-events: none;
}
.custom-select__item {
  display: inline-block;
  width: 100%;
  padding: 8px 0;
  font-size: 16px;
  line-height: 1.5;
  background-color: var(--color-white);
  border: none;
  cursor: pointer;
}
.custom-select__item:hover, .custom-select__item:focus {
  color: var(--color-gray-dark-light);
}
.custom-select__scrollbar {
  width: 100%;
  max-height: 120px;
  overflow-y: auto;
}
.custom-select__scrollbar::-webkit-scrollbar-track {
  background-color: transparent;
  border-radius: 10px;
  -webkit-box-shadow: none;
}
.custom-select__scrollbar::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #fff;
}
.custom-select__scrollbar::-webkit-scrollbar-thumb {
  background-color: var(--color-border);
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px var(--color-border);
}

/* Прозрачность формы автозаполнения start */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  color: var(--color-dark);
  background: transparent;
  -webkit-box-shadow: 0 0 0 1000px var(--color-white) inset;
  transition: background-color 0s ease-in-out 0s;
  -webkit-text-fill-color: var(--color-dark);
}

