  fill: var(--color-gray-dark-light);
}

.btn {
  padding: 0;
  border: none;
  outline: none;
}

.main-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 7px 31px;
  color: var(--color-white);
  font-weight: 600;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  background-color: var(--color-btn);
  border: 1px solid var(--color-btn);
  border-radius: 90px;
  outline: none;
  transition: all 0.3s;
}
@media screen and (min-width: 1448px) {
  .main-btn {
    font-size: 16px;
  }
}
.main-btn--big {
  padding: 8px 18px !important;
  font-size: 16px !important;
}
@media screen and (min-width: 768px) {
  .main-btn--big {
    padding: 10px 39px !important;
