  display: flex;
  gap: 16px;
  align-items: center;
  justify-self: start;
  padding: 6px 0;
}
.checkbox--error .checkbox__fake-input {
  border-color: var(--color-red);
}
.checkbox--stretch {
  height: 100%;
}
.checkbox--stretch .checkbox-col {
  height: 100%;
}
.checkbox--required {
  flex-direction: column-reverse;
  gap: 0;
  align-items: flex-start;
}
.checkbox:hover .checkbox__fake-input {
  border-color: var(--color-btn);
}
.checkbox__input {
  position: absolute;
  display: none;
  width: 16px;
  height: 16px;
  opacity: 0;
}
.checkbox__fake-input {
  display: inline-block;
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  background: var(--color-white) no-repeat center;
  border: 1px solid var(--color-gray-dark-light);
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.25s ease;
}
.checkbox__input[disabled] + .checkbox__fake-input, .checkbox__input[disabled] ~ .checkbox__fake-input {
  border: 1px solid var(--border-button-disabled);
  cursor: not-allowed;
}
.checkbox__input[disabled]:checked + .checkbox__fake-input, .checkbox__input[disabled]:checked ~ .checkbox__fake-input {
  cursor: not-allowed;
  opacity: 0.5;
}
.checkbox__input:checked + .checkbox__fake-input, .checkbox__input:checked ~ .checkbox__fake-input {
  background: url("../{{icon}}/icon__checkbox-view") no-repeat center, var(--color-btn);
  background-size: 20px;
  border-color: var(--color-btn);
}
.checkbox__input[disabled]:checked + .checkbox__fake-input, .checkbox__input[disabled]:checked ~ .checkbox__fake-input {
  opacity: 0.5;
}
.checkbox__label {
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
  cursor: pointer;
  user-select: none;
}
@media screen and (max-width: 1023px) {
  .checkbox__label {
    font-size: 14px;
  }
}
.checkbox__input[disabled] ~ .checkbox__label {
  cursor: not-allowed;
}
.checkbox__input[disabled] + .checkbox__fake-input, .checkbox__input[disabled] ~ .checkbox__fake-input {
  background-color: var(--color-grey-light);
}
.checkbox__color {
  width: 20px;
  height: 20px;
  border: 1px solid #ced3d9;
  border-radius: 30px;
}
.checkbox__color--white {
  background: var(--color-checkbox-white);
}
.checkbox__color--black {
