.toggle-switch__label {
  position: relative;
  display: block;
  width: 48px;
  height: 24px;
  text-indent: -9999px;
  background-color: var(--color-border);
  border: 1px solid var(--color-border);
  border-radius: 100px;
  cursor: pointer;
}
.toggle-switch__label::after {
  position: absolute;
  top: 3px;
  left: 3px;
  width: 16px;
  height: 16px;
  background-color: var(--color-white);
  border-radius: 90px;
  transition: 0.3s;
  content: "";
}
.toggle-switch__label--active {
  background-color: var(--color-btn-hover);
}
.toggle-switch__label--active::after {
  left: calc(100% - 3px);
  transform: translateX(-100%);
}
.toggle-switch__label:hover {
  border-color: var(--color-btn-hover);
}
.toggle-switch__label:active::after {
  width: 20px;
}

.toggle-switch-input {
  display: none;
}
.toggle-switch-input:checked + .toggle-switch__label {
  background-color: var(--color-btn-hover);
}
.toggle-switch-input:checked + .toggle-switch__label::after {
  left: calc(100% - 3px);
  transform: translateX(-100%);
