/* flex-basis */
.fb {
  &-25 {
    flex-basis: 25%;
  }

  &-40 {
    flex-basis: 40%;
  }

  &-45 {
    flex-basis: 45%;
  }

  &-50 {
    flex-basis: 50%;
  }

  &-75 {
    flex-basis: 75%;
  }

  &-100 {
    flex-basis: 100%;
  }
}

/* /flex-basis */

/* flex-direction */
.flex {
  &-row {
    flex-direction: row;
  }

  &-row-reverse {
    flex-direction: row-reverse;
  }

  &-col {
    flex-direction: column;
  }

  &-col-reverse {
    flex-direction: column-reverse;
  }
}

/* /flex-direction */

/* flex */
.flex {
  &-1 {
    flex: 1 1 0%;
  }

  &-auto {
    flex: 1 1 auto;
  }

  &-initial {
    flex: 0 1 auto;
  }

  &-none {
    flex: none;
  }
}

/* /flex */

/* flex-grow */
.flex-grow {
  flex-grow: 1;

  &-0 {
    flex-grow: 0;
  }
}

/* /flex-grow */

/* flex-wrap */
.flex {
  &-wrap {
    flex-wrap: wrap;
  }

  &-wrap-reverse {
    flex-wrap: wrap-reverse;
  }

  &-nowrap {
    flex-wrap: nowrap;
  }
}

/* /flex-wrap */

/* align-items */
.items {
  &-start {
    align-items: flex-start;
  }

  &-end {
    align-items: flex-end;
  }

  &-center {
    align-items: center;
  }

  &-baseline {
    align-items: baseline;
  }

  &-stretch {
    align-items: stretch;
  }
}

/* /align-items	 */

/* justify-content */
.justify {
  &-start {
    justify-content: flex-start;
  }

  &-end {
    justify-content: flex-end;
  }

  &-center {
    justify-content: center;
  }

  &-between {
    justify-content: space-between;
  }

  &-around {
    justify-content: space-around;
  }

  &-evenly {
    justify-content: space-evenly;
  }
}

/* /justify-content */

/* align-self */
.self {
  &-auto {
    align-self: auto;
  }

  &-start {
    align-self: flex-start;
  }

  &-end {
    align-self: flex-end;
  }

  &-center {
    align-self: center;
  }

  &-stretch {
    align-self: stretch;
  }

  &-baseline {
    align-self: baseline;
  }
}

/* /align-self */
