.wrapper-notification {
  position: fixed;
  z-index: 9998;
  display: inline-flex;
  flex-direction: column;
  padding: 20px;
  visibility: hidden;
  opacity: 0;
  transition: all $transition-primary;

  &--toast {
    width: 100%;
    max-width: 680px;
  }

  &--message,
  &--message-window {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    align-items: center;
    justify-content: center;
    padding: 10px;

    .notification {
      width: 100%;
      max-width: 48 0;
    }
  }

  &--message {
    position: absolute;
  }

  &--message-window {
    position: fixed;
    background-color: rgb(24 17 34 / 70%);
    cursor: pointer;
  }

  // positions
  &--bottom {
    &-right {
      right: 0;
      bottom: 0;
    }

    &-left {
      bottom: 0;
      left: 0;
    }
  }

  &--top {
    &-right {
      top: 0;
      right: 0;
    }

    &-left {
      top: 0;
      left: 0;
    }
  }

  &.show {
    visibility: visible;
    opacity: 1;
  }
  // /positions
}

.notification {
  $p: &;

  position: relative;
  z-index: 9998;
  display: inline-flex;
  align-items: flex-start;
  margin: 10px 0;
  padding: 16px;
  background-color: var(--bg-primary);
  border: 1px solid;
  border-radius: 8px;
  visibility: hidden;
  opacity: 0;
  transition: all $transition-primary;

  // Types
  &--preloader {
    &::before {
      background: url("data:image/svg+xml;base64, ");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      transform: rotate(0);
      transform-origin: center center;
      animation: animeSpinnerIcon 1.2s ease infinite;
    }
  }

  &--neutral {
    border-color: var(--color-toast-neutral);
    #{$p} {
      &__icon {
        svg {
          fill: var(--color-toast-neutral);
        }
      }

      &__title {
        color: var(--color-toast-neutral);
      }
    }
  }

  &--info {
    border-color: var(--color-toast-info);
    #{$p} {
      &__icon {
        svg {
          fill: var(--color-toast-info);
        }
      }

      &__title {
        color: var(--color-toast-info);
      }
    }
  }

  &--success {
    border-color: var(--color-toast-success);
    #{$p} {
      &__icon {
        svg {
          fill: var(--color-toast-success);
        }
      }

      &__title {
        color: var(--color-toast-success);
      }
    }
  }

  &--warning {
    border-color: var(--color-toast-warning);
    #{$p} {
      &__icon {
        svg {
          fill: var(--color-toast-warning);
        }
      }

      &__title {
        color: var(--color-toast-warning);
      }
    }
  }

  &--error {
    border-color: var(--color-toast-error);
    fill: #fff;
    #{$p} {
      &__icon {
        svg {
          fill: var(--color-toast-error);
        }
      }

      &__title {
        color: var(--color-toast-error);
      }
    }
  }

  .wrapper-notification--bottom-right &,
  .wrapper-notification--bottom-left & {
    transform: translateY(20px);
  }

  .wrapper-notification--top-right &,
  .wrapper-notification--top-left & {
    transform: translateY(-20px);
  }

  &__icon {
    margin: 0 10px 0 0;

    svg {
      width: 21px;
      height: 21px;
    }
  }

  &__text {
    flex-grow: 1;
    align-self: center;
    font-size: 16px;
    line-height: normal;

    p {
      margin: 0;
    }
  }

  &__title {
    font-weight: bold;
  }

  &__close {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 21px;
    height: 21px;
    margin: 0 0 0 10px;
    padding: 0;
    background-color: transparent;
    border: 0;
    border-radius: 100px;

    svg {
      width: 16px;
      height: 16px;
      pointer-events: none;
    }
  }
  $p: &;

  &.show {
    transform: translateY(0);
    visibility: visible;
    opacity: 1;
  }
  // /Types
}
